<template>
  <div class="my">
    <header>
      <div class="header-returns">
        <i class="iconfont icon-fanhui" @click="backNextPage"></i>
        <i class="iconfont icon-zhuye"></i>
      </div>
      <div class="loginOrRegister" @click="goLogin" v-show="!loginStatus">
        登录/注册
      </div>
      <div class="userMsg" v-show="loginStatus">
        <div class="userImg"><img :src="userInfo.imgUrl" alt="" /></div>
        <div class="userName">
          <span>{{ userInfo.nickName }}</span>
        </div>
      </div>
    </header>
    <section>
      <ul>
        <li @click="goToMyPage">
          <i class="iconfont icon-wode"></i> <span>个人中心</span>
        </li>
        <li @click="goPathAddress">
          <i class="iconfont icon-tianjia1"></i> <span>地址管理</span>
        </li>
        <li><i class="iconfont icon-liebiao"></i> <span>商品管理</span></li>
        <li><i class="iconfont icon-shoucang"></i> <span>我的收藏</span></li>
        <li><i class="iconfont icon-tixing1"></i> <span>问题反馈</span></li>
      </ul>
      <div class="loginOut">
        <button @click="loginOut" :disabled="!loginStatus">退出登录</button>
      </div>
    </section>
    <Tabber></Tabber>
  </div>
</template>

<script>
import Tabber from "../components/common/Tabber.vue";
import { mapState } from "vuex";
export default {
  name: "My-vue",
  components: {
    Tabber,
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.user.userInfo,
    }),
    ...mapState({
      loginStatus: (state) => state.user.loginStatus,
    }),
  },
  methods: {
    backNextPage() {
      this.$router.push("/home");
    },
    goLogin() {
      this.$router.push("/login");
    },
    //退出登录
    loginOut() {
      this.loginStatus = false;
      this.userInfo = null;
      localStorage.removeItem("teaUserInfo");
      this.$router.go(0);
    },
    goToMyPage() {
      if (this.loginStatus) {
        this.$router.push("/mymessage");
      }
    },
    //进行地址管理
    goPathAddress() {
      this.$router.push("/address");
    },
  },
};
</script>

<style scoped lang="less">
.my {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
header {
  width: 100%;
  height: 4.5rem;
  z-index: 999;
  background-color: #b0352f;
  .header-returns {
    width: 100%;
    height: 25%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    i {
      width: 0.93rem;
      height: 0.93rem;
      margin: 0 0.3rem;
      color: #fff;
      background-color: rgba(0, 0, 0, 0.3);
      border-radius: 50%;
      font-size: 0.6rem;
      text-align: center;
      line-height: 0.93rem;
    }
  }
  .loginOrRegister {
    height: 25%;
    width: 40%;
    margin: 1rem auto;
    text-align: center;
    background-color: rgb(235, 161, 82);
    color: #fff;
    font-size: 0.5rem;
    font-weight: 500;
    line-height: 1.125rem;
  }
  .userMsg {
    margin: 0 auto;
    width: 30%;
    height: 60%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .userImg {
      width: 70%;
      height: 70%;
      border-radius: 50%;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .userName {
      width: 100%;
      height: 30%;
      text-align: center;
      span {
        font-size: 0.5rem;
        color: #fff;
      }
    }
  }
}
section {
  width: 100%;
  margin-top: 0.5rem;
  flex: 1;
  ul {
    width: 90%;
    height: 70%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    li {
      box-sizing: border-box;
      width: 100%;
      border-top: 1px solid #ccc;
      margin-top: 0.3rem;
      padding: 0 0.6rem;
      font-size: 0.5rem;
      font-weight: 500;
      display: flex;
      justify-content: space-between;
      align-items: center;
      i {
        font-size: 0.7rem;
        font-weight: 500;
      }
    }
  }
  .loginOut {
    margin-top: 0.5rem;
    width: 100%;
    height: 1.2rem;
    text-align: center;
    line-height: 1.2rem;
    button {
      width: 25%;
      height: 80%;
      border: 0;
      background-color: rgb(58, 164, 58);
      color: #fff;
      font-size: 0.5rem;
      font-weight: 500;
    }
  }
}
</style>
